import React,{ Component } from "react";
import {Link} from 'react-router-dom';
import { connect } from "react-redux";
import {Switch,Input,Button,Form, Row, Col, Layout, Table} from 'antd';
import delivePublic from '../../styles/setting/delivePublic.css'
import {settingSmspro,sendSmscene} from '../../actions/setting'
import PropTypes from 'prop-types'
const {  Footer} = Layout;
const FormItem = Form.Item;
let checkSwitchs = true;


@Form.create()
@connect(
    ({
        app:{setting:{sceneList}}
    }) => ({sceneList}),
    {settingSmspro,sendSmscene}
)
export default class SmsRemind extends Component{
    static propTypes = {
        settingSmspro: PropTypes.func.isRequired,
    }
    static defaultProps = {
        settingSmspro: ()=>{},
    }
    componentDidMount(){
        this.props.sendSmscene()
    }
    handleSelectChange = (value) => {
        this.props.form.setFieldsValue({
          note: `Hi, ${value === 'male' ? 'man' : 'lady'}!`,
        });
      }
      onChange1(checked) {
        checkSwitchs=checked
      }
      handleSubmit = (e) => {
        e.preventDefault();
        const {settingSmspro} = this.props;
        this.props.form.validateFields((err, values) => {
          if (!err) {
            settingSmspro({params:{type:'aliyun',config:[values.KeyID,values.KeySecret],status:checkSwitchs}})
          }
        });
      }
  render(){
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 },
        };
        const data=[];
        const {sceneList} = this.props;
        for(let i of sceneList){
            data.push({
                key:i.id,
                id:i.id,
                name:i.name,
                sign:i.sign,
                signature:i.signature,
                provider_template_id:i.provider_template_id
            })
        }
        const columns = [{
            title: '',
            dataIndex: 'name',
            key: 'name',
        }, {
            title: 'ID',
            dataIndex: 'id',
            key: 'id',
        }, {
            title: '',
            dataIndex: 'signature',
            key: 'signature',
        }, {
            title: '',
            key: 'action',
            render: (text,datas) => {
            let path={
                pathname:'/setting/smsEdit',
                state:datas,
            }
            return(
            <span>
                <Link to={path}>
                        
                </Link>
            </span>
            )}
        }];
    return(
        <div className={delivePublic.tabPane}>
            <Row className={delivePublic.tabTop}>
                <Col span={18}  className={delivePublic.tabLeftForm}>
                    <Form  onSubmit={this.handleSubmit}>
                        <FormItem
                        {...formItemLayout}
                        label=""
                        >
                            <span className="ant-form-text"><a href="https://help.aliyun.com/document_detail/55327.html?from=fashop"></a></span>
                        </FormItem>
                        <FormItem
                        label="Access KeyID"
                        labelCol={{ span: 5 }}
                        wrapperCol={{ span: 12 }}
                        >
                        {getFieldDecorator('KeyID', {
                            rules: [{ required: true, message: '' }],
                        })(
                            <Input placeholder=""  />
                        )}
                        </FormItem>
                        <FormItem
                        label="Access KeySecret"
                        labelCol={{ span: 5 }}
                        wrapperCol={{ span: 12 }}
                        >
                        {getFieldDecorator('KeySecret', {
                            rules: [{ required: true, message: '' }],
                        })(
                            <Input placeholder=""  />
                        )}
                        </FormItem>
                        <FormItem
                        wrapperCol={{ span: 12, offset: 3 }}
                        >
                            <Button type="primary" htmlType="submit">
                                
                            </Button>
                        </FormItem>
                    </Form>
                </Col>
                <Col span={2} offset={4}>
                    <Switch style={{float: 'right',}} defaultChecked onChange={this.onChange1.bind(this)} />
                </Col>
            </Row>
            <Row type="flex" justify="space-between"  align="middle" className={delivePublic.tabCenter}>
                <Col span={4}>
                     <a href="javascript:;"></a>
                </Col>
                <Col span={2}>
                    <Switch style={{float: 'right',}} defaultChecked onChange={this.onChange} />
                </Col>
            </Row>
            <Footer className={delivePublic.tabFooter}>
                <h3></h3>
                <Table
                    columns={columns}
                    dataSource={data}
                />
            </Footer>
        </div>
    )
  }
}
